import "./header.scss";

document.addEventListener("DOMContentLoaded", () => {
  const navLinks = document.querySelectorAll(".parent > .navigation-link");
  const hamburger = document.querySelector("#nav-toggle");
  const navigation = document.querySelector("#navigation-wrapper");

  function toggleMenu (navLink, event) {
    event.stopPropagation();
    const isOpened = navLink.parentNode.classList.contains('open');
    if (isOpened) {
      navLink.parentNode.classList.remove("open");
    } else {
      navLink.parentNode.classList.add("open");
    }
  }

  //toggling the menu on click on the element
  navLinks.forEach((navLink) => {
    const subMenuLinks = navLink.parentNode.querySelectorAll('.sub-navigation .navigation-link');
    navLink.addEventListener('click', event => {
      toggleMenu(navLink, event);
    });
    navLink.addEventListener('keypress', event => {
      toggleMenu(navLink, event);
    });
    subMenuLinks.forEach((subLink) => {
      subLink.addEventListener('focusout', event => {
        if(subLink === subMenuLinks[subMenuLinks.length - 1 ]) {
          subLink.parentNode.parentNode.parentNode.classList.remove("open");
        }
      });
    });
  });

  /* Mobile menu handling */
  hamburger.addEventListener('click', event => {
    event.stopPropagation();
    const isOpened = navigation.classList.contains('open');
    if (isOpened) {
      navigation.classList.remove("open");
      hamburger.classList.remove("close");
    } else {
      navigation.classList.add("open");
      hamburger.classList.add("close");
    }
  });
});

